import React, { useEffect, useState } from 'react'
import '../../App.css'
// import { getForcast } from '../../services/api.service'
import {weatherImage } from './dommydata'
import moment from 'moment-timezone'
// import Card from '../../components/Card'
import { WiDayWindy, WiDirectionDown, WiDirectionUp, WiHumidity, WiSunrise, WiSunset, WiThermometer } from 'react-icons/wi'
import WeatherImg from '../../components/WeatherImg'
import { getForcast, getallForcast } from '../../services/api.service'

let bgColor = {
    1: 'linear-gradient(to right, #fc4a1a, #f7b733)',
    0: 'linear-gradient(to right, #0f2027, #203a43, #2c5364)'
}
const getDay = {
    1: 'day',
    0: 'night'
}

const Dashboard = () => {
    const [weatherData, SetWeatherData] = useState({})
    const [forcastData, SetforcastData] = useState({})
    const [hrsLocation, SethrsLocation] = useState(0)
    const [dateRange, SetdateRange] = useState(7)
    const [date_time, Setdate_time] = useState(7)
    // const [target_date_time, SetTarget_date_time] = useState(7)
    // SetdateRange(7)

    useEffect(() => {
        // SetWeatherData(dommyHistory)

        // SetCurrentDate({ hr_loc: forcastData?.hourly?.time.indexOf(moment.tz().format("yyyy-MM-DDTHH:00")) })
        if (Object.getOwnPropertyNames(weatherData).length > 0) {
            getallForcast(weatherData?.city?.coord).then(forcast => {
                // console.log({ forcast });
                SetforcastData(forcast)
                let date = moment().format("yyyy-MM-DDTHH:00")
                console.log({date});
                SethrsLocation(forcast?.hourly?.time.indexOf(date))

            })
        }
    }, [weatherData])

    function searchLocation(e) {
        e.preventDefault()

        const data = new FormData(e.currentTarget);
        getForcast(data.get('location') ? data.get('location') : 'bhubaneswar').then(current_weather => {
            // console.log(JSON.stringify(current_weather));
            SetWeatherData(current_weather)
            // SetWeatherDataList(forcast.list.slice(arrayLen.start, arrayLen.end))
        })
    }

    setInterval(()=>{
        Setdate_time(moment().format("hh:mm:ss A "))
    },1000)

    return (
        // <React.Fragment>
        <div style={{ background: bgColor[forcastData?.current_weather?.is_day], color: 'white', height: '100vh', padding: '1rem 10rem' }}>
            <div>
                <form onSubmit={e => { searchLocation(e) }} style={{ display: 'flex', justifyContent: 'center' }}>
                    <input type="text" name="location" placeholder=' Search location here' />
                    <button type='submit' value='submit' >Get Weather Data</button>
                </form>
            </div>



            {(forcastData) &&
                <div style={{ display: 'flex', flexDirection: 'column' }}>
                    <div className="curentData">
                        <div>{moment.tz().format("dddd, DD MMMM yyyy")} | Local time {date_time}</div>

                        <div className='location'>{weatherData?.city?.name},{weatherData?.city?.country}</div>
                        <div className='current_weather'>{(forcastData?.current_weather?.weathercode) ? weatherImage[forcastData?.current_weather?.weathercode][getDay[forcastData?.current_weather?.is_day]]['description'] : ''}  </div>

                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                            <div className="temp_img">
                                {(forcastData?.current_weather?.weathercode) ? <WeatherImg weathercode={`${forcastData?.current_weather?.weathercode}`} day={`${getDay[forcastData?.current_weather?.is_day]}`} /> : ""}

                            </div>
                            <div className='current_temp'>{forcastData?.current_weather?.temperature} {forcastData?.hourly_units?.temperature_2m}</div>
                            <div className="temp_det" style={{ display: 'flex', flexDirection: 'column' }}>
                                <div> <WiThermometer />Real Temp {forcastData?.current_weather?.temperature} {forcastData?.hourly_units?.temperature_2m}</div>
                                <div> <WiHumidity />Humidity {forcastData?.hourly?.relativehumidity_2m[hrsLocation]} {forcastData?.hourly_units?.relativehumidity_2m}</div>
                                <div> <WiDayWindy />wind {forcastData?.current_weather?.windspeed} {forcastData?.hourly_units?.windspeed_10m}</div>
                            </div>
                        </div>
                        <div style={{ display: 'flex', flexDirection: 'row', justifyContent: 'space-between' }}>
                            <div><WiSunrise /> Rise :  {moment(forcastData?.daily?.sunrise[0]).format("hh:mm A")}</div>
                            <div><WiSunset /> Set  : {moment(forcastData?.daily?.sunset[0]).format("hh:mm A")}</div>
                            <div><WiDirectionUp /> High  : {forcastData?.daily?.apparent_temperature_max[0]} {forcastData?.daily_units?.apparent_temperature_max[0]}</div>
                            <div><WiDirectionDown /> Low  : {forcastData?.daily?.apparent_temperature_min[0]} {forcastData?.daily_units?.apparent_temperature_min[0]}</div>
                        </div>

                    </div>
                    <div className="heading">HOURLY FORECAST</div>
                    <div className="forcast">
                        {
                            forcastData.hourly?.time.slice(hrsLocation, hrsLocation + dateRange).map((el, i) => <div className='forcast_element'>
                                <div className="date_time" key={forcastData?.hourly?.time[hrsLocation + dateRange + i].split('T')[1]}>{forcastData?.hourly?.time[hrsLocation + dateRange + i].split('T')[1]}</div>
                                <WeatherImg weathercode={`${forcastData?.hourly?.weathercode[hrsLocation + dateRange + i]}`} day={`${getDay[forcastData?.hourly?.is_day[hrsLocation + dateRange + i]]}`} />

                                <div className="date_time" key={forcastData?.hourly?.temperature_2m[hrsLocation + dateRange + i]}>{forcastData?.hourly?.temperature_2m[hrsLocation + dateRange + i]} {forcastData?.hourly_units?.temperature_2m}</div>
                            </div>)}

                    </div>

                    <div className="heading">DAILY FORECAST</div>
                    <div className="forcast">
                        {forcastData?.daily?.time.map((el, i) => <div className='forcast_element'>
                            <div className="date_time" key={forcastData?.daily?.time[i]}>{forcastData?.daily?.time[i]}</div>
                            <WeatherImg weathercode={`${forcastData?.daily?.weathercode[i]}`} day={getDay["1"]} />

                            <div className="date_time" key={forcastData?.daily?.temperature_2m_max[i]}>{forcastData?.daily?.temperature_2m_max[i]} {forcastData?.hourly_units?.temperature_2m}</div>
                        </div>)}

                    </div>

                </div>
            }
        </div>
        // </React.Fragment>
    )
}

export default Dashboard
