const moment = require("moment-timezone");
let date = moment.tz("Asia/Kolkata").format("yyyy-MM-DDTHH:00");
let loc = this.forcast?.hourly?.time.indexOf(date);
let date_range = 7;
let x = this.forcast?.hourly?.time.slice(loc, loc + date_range);
let y = this.forcast?.hourly?.temperature_2m.slice(loc, loc + date_range);
// let x = forcast?.hourly?.time.slice(loc,loc+4)

console.log({ x, date, loc });

this.forcast.hourly.time = x;
this.forcast.hourly.temperature_2m = y;

console.log(JSON.stringify(this.forcast?.hourly?.time));
console.log(JSON.stringify(this.forcast?.hourly?.temperature_2m));
