import React from 'react'
import { weatherImage } from "../pages/dashboard/dommydata";
import '../App.css'

const WeatherImg = (props) => {
    console.log(props);
   
    return (
        <div>
            <img className='wether_icon' src={weatherImage[props.weathercode][props.day]['image']} alt='weather logo' />

        </div>
    )
}

export default WeatherImg
