import axios from "axios";
import { endpoints } from "../apiList";
const api_key = "68a412eca6c8eb03b814cc6c15ffde4e";
// const weath = "f5f3f14c13f4fc552a2caa4c2a3b720c";

export const getForcast = (location) => {
  const options = {
    method: "GET",
    url: `${endpoints.weather_history_3hr}?q=${location}&units=metric&lang=en&appid=${api_key}`,
  };

  return new Promise((resolve, reject) => {
    axios
      .request(options)
      .then(function (response) {
        // console.log(response);
        resolve(response.data);
      })
      .catch(function (error) {
        console.error(error);
      });
  });
};

